import * as React from "react";
import { Form } from "./Form";
import { Field } from "./Field";

export const LoginForm: React.SFC = () => {
  return (
    <Form
      action=""
      render={() => (
        <React.Fragment>
          <div className="col col-md-4">
            <Field id="email" label="Email" placeholder="Email Address" required="required" type="email" />
          </div>
          <div className="col col-md-4">
            <Field id="password" label="Password" placeholder="Password" required="required" type="password" />
          </div>
        </React.Fragment>
      )}
    />
  );
};


